import React, { useEffect, useState } from "react";

import FunctionButton from "./FunctionButton/FunctionButton";
// import { MenuOutlined } from "@ant-design/icons";
import { useIsMobileView } from "../../hooks/useIsMobileView";
import styles from "./Header.module.scss";

const Header = () => {
  const isMobileView = useIsMobileView();
  const [menuCollapsed, setMenuCollapsed] = useState(isMobileView || false);

  useEffect(() => {
    if (isMobileView) {
      setMenuCollapsed(true);
    } else {
      setMenuCollapsed(false);
    }
  }, [isMobileView]);

  const toggleMenu = () => {
    if (isMobileView) {
      setMenuCollapsed(!menuCollapsed);
    }
  };
  return (
    <div className={styles.header}>
      {isMobileView && (
        // <MenuOutlined className={styles.menuIcon} onClick={toggleMenu} />
        <img
          src="./static/images/header/crown.png"
          alt="crown"
          onClick={toggleMenu}
          className={styles.menuIcon}
        />
      )}
      {!menuCollapsed && !isMobileView && (
        <div className={styles.iconWrap}>
          <img src="./static/images/header/headerlogo.png" alt="bnb brown" />
        </div>
      )}

      {!menuCollapsed && (
        <div className={styles.functionWrapper}>
          <FunctionButton title="Support" isBorder={true} />
          <FunctionButton
            title="Telegram"
            isBorder
            bgColor="#12D8F3"
            isDropdown
          />
          <FunctionButton title="Audit" isBorder bgColor="#126CF3" isDropdown />
          <FunctionButton title="Help" isBorder bgColor="#1B9854" />
          <FunctionButton
            title="Presentation"
            isBorder
            bgColor="#F36312"
            isDropdown
          />
        </div>
      )}
      <div className={styles.walletButton}>
        <FunctionButton title="Connect Wallet" bgColor="#F39C12" isSpecial />
      </div>
    </div>
  );
};

export default Header;
