import React from "react";

import { Row, Col, Input } from "antd";
import classNames from "classnames";
import StakeButton from "./StakeButton/StakeButton";
import styles from "./PlanCard.module.scss";
const PlanCard = (props) => {
  const { info } = props;
  const { key, profit, totalReturn, withdrawTime, period, calculatedReturn } =
    info;
  const blueBg = key > 3;
  return (
    <div className={styles.planCard}>
      <div className={classNames(styles.planBar, { [styles.blueBg]: blueBg })}>
        {`Plan ${key}`}
      </div>
      <div className={styles.profitDetail}>
        <Row gutter={[40, 8]}>
          <Col span={12}>
            <p>Daily Profit</p>
            <span className={blueBg ? styles.blueFont : styles.yellowFont}>{`${
              profit / 10
            }%`}</span>
          </Col>
          <Col span={12}>
            <p>Total Return</p>
            <span
              className={blueBg ? styles.blueFont : styles.yellowFont}
            >{`${totalReturn}%`}</span>
          </Col>
          <Col span={12}>
            <p>Withdraw time</p>
            <span
              style={{ fontSize: "30px" }}
              className={blueBg ? styles.blueFont : styles.yellowFont}
            >
              {withdrawTime}
            </span>
          </Col>
          <Col span={12}>
            <p>Days</p>
            <span className={blueBg ? styles.blueFont : styles.yellowFont}>
              {period}
            </span>
          </Col>
          <Col span={24}>
            <p>Enter Amount</p>
            <span className={blueBg ? styles.blueFont : styles.yellowFont}>
              <Input placeholder="" />
            </span>
          </Col>
        </Row>
      </div>

      <div className={styles.getBnb}>
        <p>In 14 Days You will Get </p>
        <span
          className={blueBg ? styles.blueFont : styles.yellowFont}
        >{`${calculatedReturn}BNB`}</span>
      </div>
      <StakeButton
        title="Stake"
        className={blueBg ? styles.blueBg : styles.yellowBg}
      />
    </div>
  );
};

export default PlanCard;
